@import url("https://fonts.googleapis.com/css2?family=JetBrains+Mono&display=swap");

/* defining variables */
$font-code: "JetBrains Mono", monospace;
$font-base: $font-code;
// $font-base: -apple-system, Helvetica, arial, sans-serif;
$color-base: rgba(0, 0, 0, 0.8);
$color-code-border: #dadada;
$color-code-bg: #f8f8f8;
$color-code-bg-inline: #e6e6e6;
$color-link: $color-base;
$color-link-hover: rgba(0, 0, 0, 0.6);
$color-nav-link: $color-link-hover;
$color-bib: rgba(0, 0, 0, 0.65);

/* general */

h1 {
    font-size: 24px;
}

h2 {
    font-size: 22px;
}

h3 {
    font-size: 20px;
}

h4 {
    font-size: 18px;
}

h5 {
    font-size: 16px;
}

h6 {
    font-size: 14px;
}

.page-title {
    font-weight: bold;
    font-size: 28px;
    padding-bottom: 10px;
}

html {
    font-family: $font-base;
    color: $color-base;
    line-height: 1.5em;
    text-size-adjust: 100%;
    padding: 6px;
}

a {
    color: $color-link;
}

/* remove underline during hover */
a:hover {
    text-decoration: none;
    color: $color-link-hover;
}

/* remove padding and bullets of lists */
.clean-ul ul {
    padding: 0;
    list-style-type: none;
    margin-top: 0px;
    margin-bottom: 0px;
}

/* turn list horizontal with small margin */
.horizontal-li li {
    display: inline;
    margin-right: 5px;
}

.wrap {
    margin: 0 auto;
    min-width: 300px;
    max-width: 700px;
}

// nav bar

#author-name {
    font-size: 30px;
    color: #272727;
    display: inline-block;
}

#nav-links a:hover {
    font-weight: bold;
}

.nav {
    padding: 50px 0 50px 0;
}

.nav a {
    color: $color-nav-link;
    text-decoration: none;
}

.nav-ul {
    display: inline-block;
    margin: 0;
    margin-left: 20px;
}

.nav-li {
    list-style-type: none;
    margin: 0 5px 0 10px;
}

.alignable {
    display: inline-block;
}

.pull-left {
    @extend .alignable;
    float: left;
}

.pull-right {
    @extend .alignable;
    float: right;
}

.project-index {
    margin-top: 15px;
    margin-bottom: 15px;
}

.page-index-title {
    margin-bottom: 5px;
    font-weight: bold;
    font-size: 18px;
}

.project-date {
    font-size: 12px;
    font-weight: bold;
}

.post-links {
    margin-top: 5px;
}

.bibliography {
    color: $color-bib;
    font-size: 12px;
}

.table-bordered {
    border-collapse: collapse;
    text-align: center;

    > table,
    td,
    th {
        border: 1px solid black;
    }
}

.tags li {
    display: inline;
    margin: 0 0 0 0;
}

// add comma to tags list
.tags li:after {
    content: ", ";
}

.tags li:last-child:after {
    content: "";
}

/* code syntax highlighting */

code {
    font-family: $font-code;
}

pre {
    font-family: $font-code;
    font-size: 14px;
}

// enable scrolling for long lines of code
.highlight pre {
    overflow-x: auto;
    margin-top: 0;
    padding-top: 0;
}

// add background for inline code
.highlighter-rouge {
    background-color: $color-code-bg-inline;
}

// line number background color
.rouge-gutter {
    color: $color-code-border;
}

.filename {
    overflow-x: auto;
    font-family: $font-code;
    background-color: $color-code-border;
    margin-bottom: 0;
    padding-bottom: 1px;
    padding-left: 1.6em;
    padding-top: 4px;
}

/* table formatting */

thead th {
    padding: 2.5px;
}

tbody td {
    padding: 0 5px 0 5px;
}
